// 轨距测量标准
<template>
  <div class="typeMange">
    <!-- Header -->
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark typeHeader">
          <el-button type="primary" @click="addOne">新增</el-button>
        </div>
      </el-col>
    </el-row>


    


    <!-- Main -->
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark typeMain">
          <!-- <p style="padding-bottom: 20px">标题</p> -->
          <div class="mb" style="padding-bottom: 20px;">
            <el-breadcrumb separator="/">
              <el-breadcrumb-item to="/measureStandard">测量标准</el-breadcrumb-item>
              <el-breadcrumb-item to="/measureStandardTemplate">测量标准模板</el-breadcrumb-item>
              <el-breadcrumb-item>轨距测量标准</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column label="里程" width="180">
              <template slot-scope="scope">
                <span>{{ scope.row.turnouttypename }}</span>
              </template>
            </el-table-column>
            <el-table-column label="轨距" width="180">
              <template slot-scope="scope">
                <span >{{ scope.row.reamke }}</span>
                <span v-show="false">{{ scope.row.id }}</span>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
    <!-- Alert -->
    <div v-show="editStatus || addStatus" class="typeAlert-bg"></div>
    <div v-show="editStatus || addStatus" class="typeAlert">
      <el-row>
        <el-col>
          <p style="padding-bottom: 20px">修改</p>
        </el-col>
      </el-row>
      <el-row class="edit-item">
        <el-col :span="6">
          <p>里程：</p>
        </el-col>
        <el-col :span="18">
          <el-input v-model="save.mileage" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>
      <el-row class="edit-item">
        <el-col :span="6">
          <p>轨距：</p>
        </el-col>
        <el-col :span="18">
          <el-input v-model="save.gauge" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>
      <el-row >
        <el-col  v-show="editStatus" :span="24" class="edit-submit">
          <el-button size="mini" @click="editChange" type="primary">确定修改</el-button>
          <el-button size="mini" @click="editClose">放弃修改</el-button>
        </el-col>
        <el-col  v-show="addStatus" :span="24" class="edit-submit">
          <el-button size="mini" @click="addOk" type="primary">确定</el-button>
          <el-button size="mini" @click="editClose">放弃</el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import api from "@/api/index.js";
  export default {
    data() {
      return {
        save: {
          turnoutstandardid: '', // 父级id 关联项点
          mileage: '', // 里程
          gauge: '', // 轨距
        },
        tableData: [
          // {
          //   turnouttypename: '0',
          //   reamke: '标准名称1234',
          //   id: ''
          // }
        ],
        editStatus: false,  // 修改弹窗显示
        edit: {             // 修改提交数据
          turnouttypename: '',
          reamke: '',
          id: ''
        },
        addStatus: false,  // 新增弹窗显示
        add: {
          turnouttypename: '',
          reamke: ''
        }

      }
    },
    methods: {
      // 新增
      addOne() {
        this.addStatus = true;
      },
      // 确认新增
      addOk() {
        console.log(this.edit)
        if(!this.save.mileage) {
          this.$message({
            type: 'error',
            message: '里程不正确!'
          });
          return;
        }
        if(!this.save.gauge) {
          this.$message({
            type: 'error',
            message: '轨距不正确!'
          });
          return;
        }
        api.newGaugestandard({
          turnoutid: this.save.turnoutstandardid,
          mileage: this.save.mileage,
          gauge: this.save.gauge
        }).then(res => {
          console.log(res)
          if(res.data.status == 'ok') {
            this.editStatus = false;
            this.addStatus = false;
            this.$message({
              type: 'success',
              message: '新增成功!'
            });
            this.getLists();
          } else {
            this.$message({
              type: 'error',
              message: '新增失败!'
            });
          }
        })
      },
      // 唤起修改弹窗
      handleEdit(index, row) {
        console.log(index, row);
        this.editStatus = true;
        this.edit.turnouttypename = row.turnouttypename;
        this.edit.reamke = row.reamke;
        this.edit.id = row.id;
      },
      // 唤起删除弹窗
      handleDelete(index, row) {
        console.log(index, row);
        this.$confirm('此操作将永久删除该项, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          api.delRoadClassify(row.id).then(res => {
            console.log(res)
            if(res.data.status == 'ok') {
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              this.getLists();
            } else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      // 确认修改
      editChange() {
        this.editStatus = false;
        this.addStatus = false;
        api.editRoadClassify(this.edit.id, {turnouttypename: this.edit.turnouttypename, reamke: this.edit.reamke}).then(res => {
          console.log(res)
          if(res.data.status == 'ok') {
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            this.getLists();
          } else {
            this.$message({
              type: 'error',
              message: '修改失败!'
            });
          }
        })
      },
      // 取消修改
      editClose() {
        this.editStatus = false;
        this.addStatus = false;
      },
      // 获取列表
      getLists() {
        api.getRoadClassify().then(res => {
            console.log(res);
            this.tableData = [];
            res.data.result.forEach(item => {
              this.tableData.push({
                id: item.id,
                reamke: item.reamke,
                turnouttypename: item.turnouttypename
              })
            });
        })
      },

      getGaugestandard() {
        api.getGaugestandard().then(res => {
          console.log(res.data.result)
        })
      }
    },
    created() {
      console.log(this.$route.params)
      this.save.turnoutstandardid = this.$route.params.turnoutstandardid;
      // 获取列表
      this.getLists();
      this.getGaugestandard();
    },
  }
</script>

<style lang="stylus" scoped>
  .typeHeader
      display flex
      justify-content flex-end
      padding-bottom 20px
  .typeMain
      background #fff
      border-radius 12px
      overflow hidden
      padding 20px
      .el-table
        border 1px solid rgba(0,0,0,.1)
  .typeAlert-bg
      width 100%
      height 100vh
      position fixed
      left 0
      top 0
      z-index 1
      background rgba(0,0,0,.5)
  .typeAlert
    width 350px
    height 200px
    background #fff
    position fixed
    left 50% 
    top 50% 
    transform translate(-50%, -50%)
    z-index 9
    padding 20px
    border-radius 4px
    .edit-item
      display flex
      align-items center
      padding 6px 0
    .edit-submit
      display flex
      justify-content flex-end
      padding-top 30px


</style>


